@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,menu,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,main,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,main,menu,nav,section{display:block}*[hidden]{display:none}body{line-height:1}menu,ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}form{display:flex;border-radius:8px;padding:1rem 1rem 1.2rem;background:#0F2027}@media (max-width: 700px){form{flex-direction:column;padding-bottom:1.5rem}}form .form-input{display:flex;flex-direction:column;flex:1 1}form .form-input.search{flex:3 1}form .form-input:not(:first-child){padding-left:1rem}@media (max-width: 700px){form .form-input:not(:first-child){padding-left:0;margin-top:1rem}}form .form-input label{margin-bottom:.8rem}form .form-input label[disabled]{color:rgba(255,255,255,0.3)}form .form-input input,form .form-input select{flex:1 1;border-radius:4px;border:solid 1px rgba(255,255,255,0.8);background-color:transparent;font-size:1rem;font-weight:200;padding:.5rem;color:#fff;font-family:'Roboto', sans-serif}form .form-input input:disabled,form .form-input select:disabled{color:rgba(255,255,255,0.3);border-color:rgba(255,255,255,0.3)}form .form-input input::-webkit-input-placeholder,form .form-input select::-webkit-input-placeholder{color:rgba(255,255,255,0.8)}form .form-input input:focus,form .form-input select:focus{outline:none}form .form-input input option,form .form-input select option{padding:.5rem;background:#0F2027;color:#fff}.list{background-color:#0F2027;margin:2rem 0;border-radius:8px}.list:not(:empty){border:solid 1px #fff}.list .item{display:flex;align-items:center;padding:1rem}.list .item:not(:last-child){border-bottom:solid 1px #fff}.list .item .image{max-width:80px;margin-right:1rem}.list .item .image img{width:100%}.list .item .details{flex:1 1}.list .item .details span{font-size:.9rem;font-style:italic}.list .item .details span+span{margin-left:1rem}.list .item .details .title{display:block;margin-bottom:.1rem;font-size:1.5rem;font-weight:bold;color:#fff;text-decoration:none}.list .item .details p{margin-top:.5rem}.pagination{margin-top:2rem}.pagination li{display:inline-block;padding:.5rem .8rem;border:solid 1px white}.pagination li:hover{cursor:pointer}.pagination li.active{pointer-events:none;background-color:#fff;color:#0F2027;font-weight:bold}.pagination li:not(:first-child){border-left:none}.pagination li:first-child{border-top-left-radius:8px;border-bottom-left-radius:8px}.pagination li:last-child{border-top-right-radius:8px;border-bottom-right-radius:8px}html,body{background:#0F2027;background:linear-gradient(to top, #0F2027, #203A43, #2C5364) fixed;font:20px "Roboto",sans-serif}#root{min-height:100vh;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:flex-start;color:#fff}.app{width:60vw;height:80vh;display:flex;flex-direction:column}@media (max-width: 700px){.app{width:90vw}}.app h1{margin:2rem auto;font-size:2rem;font-weight:bold}.app .loading{margin-top:2rem;padding:1rem;border-radius:8px;background:#0F2027;font-size:1.3rem;text-align:center}

